import React, { /* useState, */ useEffect } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import useService from "../../_hooks/service.hook";
import { useSelector } from "react-redux";

import routes from "./routes";

import WOW from "wowjs";
import "./app-assets/css/bootstrap.min.css";
import "./app-assets/css/animate.css";
import "./app-assets/css/common.css";
import "./app-assets/css/style.css";
import "./app-assets/css/responsive.css";

// import '@fortawesome/fontawesome-free/css/all.min.css';
import { Header } from "./components/header/Header";
import { Header2 } from "./components/header/Header2";
import { Header3 } from "./components/header/Header3";
import { Footer } from "./components/footer/Footer";
import { Footer2 } from "./components/footer/Footer2";

function GuestMarkup({ services }) {
  const wow = new WOW.WOW({
    boxClass: "wow",
    animateClass: "animated",
    mobile: false,
  });

  wow.init();
  useEffect(() => {
    wow.sync();
  }, []);

  const cur_loc = window.location.pathname;
  var Custom_Header = null;
  var Custom_Footer = null;
  if (cur_loc === "/") {
    Custom_Header = <Header />;
  } else if (
    cur_loc === "/affiliate" ||
    cur_loc === "/support" ||
    cur_loc === "/my-page" ||
    cur_loc === "/my-page-2"
  ) {
    Custom_Header = <Header3 />;
  } else if (cur_loc === "/frame01") {
    Custom_Header = null;
  } else {
    Custom_Header = <Header2 />;
  }

  if (cur_loc === "/") {
    Custom_Footer = <Footer />;
  } else if (cur_loc === "/frame01") {
    Custom_Footer = null;
  } else {
    Custom_Footer = <Footer2 />;
  }

  const session = useSelector((state) => state?.session);

  return (
    <>
      <div className="wrap">
        {Custom_Header}
        <div className="" style={{ minHeight: "100vh" }}>
          <Switch>
            {/* <Route exact path="/login">
              {!session?.user ? (
                <Login {...{ services, useService }} />
              ) : (
                <Redirect to={{ pathname: `/` }} />
              )}
            </Route> */}
            <Switch>
              {routes.map(
                (
                  { url = "", title = "", auth = false, component: Component },
                  i
                ) =>
                  auth ? (
                    <Route
                      key={i}
                      exact
                      path={`/${url}`}
                      component={() => (
                        <SecuredRoute
                          component={Component}
                          props={{ services, useService }}
                        ></SecuredRoute>
                      )}
                      title={title}
                    />
                  ) : (
                    <Route
                      key={i}
                      exact
                      path={`/${url}`}
                      component={() => (
                        <Component {...{ services, useService }} />
                      )}
                      title={title}
                    />
                  )
              )}
            </Switch>
          </Switch>
        </div>
        {Custom_Footer}
      </div>
    </>
  );
}

export default GuestMarkup;

/**
 * @function SecuredRoute
 * @param {Object} props
 * @returns
 */
function SecuredRoute({ component: Component, props }) {
  const session = useSelector((state) => state?.session);
  useEffect(() => {
    if (!session?.user) window.location = "/";
  }, [session]);

  return session?.user ? (
    <Component {...props} {...{ user: session?.user }} />
  ) : (
    <div
      style={{
        minHeight: "50vh",
        padding: 10,
        marginTop: 100,
        display: "flex",
        justifyContent: "center",
      }}
    >
      <header className="text-center" style={{ position: "relative" }}>
        <h2 className="h3 font-weight-bold">
          You are not allowed to view this page as guest
        </h2>
        <p>You must be logged in to view the content of this page</p>
      </header>
    </div>
  );
}
