import SelectInput from "./Select.component";

const currencies = {
  ALL: null,
  CNY: "cny",
  AED: "aed",
  AMD: "amd",
  ARS: "ars",
  AUD: "aud",
  BDT: "bdt",
  BHD: "bhd",
};

export default function FiatCurrency({ onChange, attributes }) {
  return (
    <SelectInput
      onChange={onChange}
      attributes={{
        ...{
          name: "fiat-currencies",
          id: "fiat-currencies",
          ...attributes,
        },
      }}
      data={currencies}
    />
  );
}
