import SelectInput from "./Select.component";
const currencies = {
  ALL: null,
  USDT: "usdt",
  BTC: "btc",
  EOS: "eos",
  ETH: "eth",
  XRP: "xrp",
};

export default function CryptoCurrency({ onChange, attributes }) {
  return (
    <SelectInput
      onChange={onChange}
      attributes={{
        ...{
          name: "crypto-currencies",
          id: "crypto-currencies",
          ...attributes,
        },
      }}
      data={currencies}
    />
  );
}
