import SelectInput from "./Select.component";
const languages = {
  ENGLISH: "eng",
  KOREAN: "krn",
};

export default function LanguageSelector({ onChange, attributes }) {
  return (
    <SelectInput
      onChange={onChange}
      attributes={{
        ...{
          name: "languages",
          id: "languages",
          ...attributes,
        },
      }}
      data={languages}
    />
  );
}
