import SelectInput from "./Select.component";
const status = {
  ALL: null,
  DISPUTED: "disputed",
  CANCELLED: "cancelled",
  COMPLETED: "completed",
  PENDING: "pending",
};

export default function OrderStatus({ onChange, attributes }) {
  return (
    <SelectInput
      onChange={onChange}
      attributes={{
        ...{
          name: "order-statuses",
          id: "order-statuses",
          ...attributes,
        },
      }}
      data={status}
    />
  );
}
