import SelectInput from "./Select.component";
const order_types = { ALL: null, BUY: "buy", SELL: "sell" };

export default function orderType({ onChange, attributes }) {
  return (
    <SelectInput
      onChange={onChange}
      attributes={{
        ...{ name: "order-type", id: "order-type", ...attributes },
      }}
      data={order_types}
    />
  );
}
