export default function SelectInput({
  onChange = () => null,
  attributes,
  data,
}) {
  return (
    <select onChange={(e) => onChange(e.target?.value)} {...attributes}>
      {typeof data == "object" && Array?.isArray(data)
        ? data?.map((item, index) => (
            <option key={index} value={item} className="text-uppercase">
              {item.replace("_", " ")}
            </option>
          ))
        : Object.keys(data)?.map((key) => (
            <option
              key={key}
              value={data[key] || ""}
              className="text-uppercase"
            >
              {key.replace("_", " ")}
            </option>
          ))}
    </select>
  );
}
