import SelectInput from "./Select.component";
const currencies = {
  All: null,
  Bank_Transfer: "bank_transfer",
  WeChat: "wechat",
  Alipay: "alipay",
  Bank_Deposit: "bank_deposit",
};

export default function PaymentMethod({ onChange, attributes }) {
  return (
    <SelectInput
      onChange={onChange}
      attributes={{
        ...{
          name: "payment-methods",
          id: "payment-methods",
          ...attributes,
        },
      }}
      data={currencies}
    />
  );
}
